@charset "utf-8";

@import "common";

/*-----------------------------------------------------------
header
-----------------------------------------------------------*/
.wordchoice {
  // background: $base-bg;
  width: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  padding: 8px 16px;
  font-size: 12px;
  border-radius: 0 0 0 3px;
  color: #888;
  &.sp {
    display: none;
  }
  button {
    padding: 0 12px;
    background: none;
    border: none;
    color: #888;
    line-height: 1;
    &.active {
      color: $base-font;
    }
    &:hover {
      cursor: pointer;
    }
  }
  @include thou {
    &.pc {
      display: none;
    }
    &.sp {
      display: block;
      top: initial;
      bottom: 10%;
    }
  }
}
.w-en,
.w-jp {
  display: none;
  &.is-active {
    display: block;
  }
}
header {
  padding: 0 10px 0 0;
  width: min(95%, 1800px);
  width: 95%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: min(5%, 45px);
  left: 50%;
  transform: translateX(-50%);
  z-index: 12;
  transition: 0.2s;
  &.headerScroll {
    backdrop-filter: blur(12px);
    padding: min(3%, 15px) 4% min(3%, 15px);
  }
  a.hlogo {
    min-width: 110px;
    &:hover {
      opacity: 1;
    }
  }
  .btn {
    width: 180px;
    height: 55px;
    border-radius: 50px;
    font-size: clamp(12px, 1.6vw, 15px);
    display: grid;
    place-content: center;
    font-weight: 600;
    color: $base-font;
    background: linear-gradient(45deg, #205b6d, #19385a);
    border: 1px solid #597180;
    margin: 0 0 0 1.5em;
    &:link,
    &:visited {
      color: $base-font;
    }
  }
  @include thou {
    top: 0;
    padding: 12px 5%;
    width: 100%;
    position: fixed;
    .btn {
      width: auto;
      padding: 0.6em 1.5em 0.5em;
      margin: 0 1.5em 0 auto;
      height: auto;
    }
    .headerUl {
      width: auto;
      display: block;
      .sp {
        display: flex;
        justify-content: flex-end;
      }
    }
    .hlogo {
      width: 110px;
    }
  }
}
/*-----------------------------------------------------------
 nav
-----------------------------------------------------------*/
nav {
  margin: auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  ul {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    li {
      margin-right: min(4%, 35px);
      &:last-child:not(.gnav__menu__item) {
        margin-right: 0;
      }
      a {
        text-decoration: none;
        display: block;
        font-family: "Shippori Mincho B1", "游明朝体", "Yu Mincho", YuMincho,
          "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", serif;
        font-weight: 300;
        font-size: 13px;
      }
    }
  }
}

/*-----------------------------------------------------------
#cover
-----------------------------------------------------------*/
.add-animation {
  /* アニメーション名 アニメーションにかかる時間 アニメーションの実行前後の状態 */
  /* zoom-fade 10秒 [実行前]最初のキーフレーム（0%） → [実行後]最後のキーフレーム（100%） */
  animation: zoom-fade 10s both;
}
@keyframes zoom-fade {
  0% {
    /*transform: scale(1.05);*/
    transform: scale(1);
  }
  100% {
    /*transform: scale(1);*/
    transform: scale(1.05);
  }
}
#cover {
  width: min(100%, 100%);
  margin: auto;
  position: relative;
  .cov-div {
    overflow: hidden;
    height: calc(100vh - 30px);
    .sld {
      height: calc(100vh - 30px);
      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .sp {
      display: none !important;
    }
  }
  .slider .slick-slide {
    margin-right: 1vw !important;
    margin-left: 1vw !important;
    .sld {
      border-radius: 20px;
      object-fit: cover;
      @include ipad {
        border-radius: 8px;
      }
    }
  }
  .logo_area {
    position: absolute;
    top: 50%;
    left: 7.5vw;
    transform: translateY(-50%);
    z-index: 1;
  }
  .text_area2 {
    position: absolute;
    z-index: 1;
    width: auto;
    top: 50%;
    transform: translateY(-50%);
    right: 7.5vw;
    p {
      font-size: clamp(18px, 4.2vw, 42px);
      text-align: left;
      &.en {
        font-size: 0.7em;
        margin-top: 20px;
      }
      &.w-en {
        font-size: clamp(18px, 4.2vw, 42px);
      }
    }
    .cen_logo p {
      text-align: center;
      font-size: 15px;
      line-height: 2;
      margin-top: 60px;
    }
  }
  .text_area {
    // position: absolute;
    // z-index: 1;
    margin-top: 35px;
    width: min(100%, 210px);
    bottom: min(6%, 60px);
    left: min(5%, 50px);
    // left: 50%;
    // transform: translateX(-50%);
    ul {
      text-align: center;
      margin: auto;
      li {
        margin-bottom: 35px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  .cv_btn {
    position: fixed;
    bottom: 20px;
    right: 20px;
    text-align: center;
    width: 208px;
    height: 208px;
    z-index: 11;
    img {
      width: 100%;
      transition: 0.2s;
    }
    a:hover {
      opacity: 1;
      img {
        transform: rotate(8deg);
      }
    }
  }
  .line_share {
    background: white;
    border-radius: 8px 0 0 8px;
    padding: 15px;
    position: fixed !important;
    bottom: 210px;
    right: 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.16);
    display: none;
    text-align: center;
    .line-it-button {
      width: 30px !important;
      height: 30px !important;
      display: none;
      margin: auto;
    }
    p {
      color: #333;
      font-size: 10px;
      font-weight: 700;
      letter-spacing: 0.04em;
      padding-top: 8px;
      line-height: 1;
    }
  }
  @media screen and (min-width: 591px) and (max-height: 580px) {
    /* 横幅800px以下で、高さ500px以下の場合 */
    .cv_btn {
      width: 320px;
      padding: 20px 1.5% 15px;
      margin: 12px auto 0;
      .btn-bottm a.btn {
        font-size: 32px;
      }
    }
  }
  @include thou {
  }
  @include mid {
    height: auto;
    padding-bottom: 2.5%;
    .logo_area {
      .mvlogo {
        width: 125px;
        margin: auto;
      }
    }
    .text_area {
      padding: 3% 5% 0 3%;
      bottom: 20px;
    }
    .cv_btn {
      width: 180px;
      height: 180px;
      right: 3%;
      border-radius: 8px 8px 0 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 2%;
      .btn-bottm a.btn {
        font-size: 20px;
        margin: auto;
        padding: 18px 15px 20px;
        min-width: 230px;
        padding-right: 35px;
      }
    }
    .line_share {
      display: block;
      .line-it-button {
        display: block;
      }
    }
  }
  @include sp {
    .logo_area {
      top: 53%;
      .mvlogo {
        width: 100px;
      }
    }
    .text_area {
      img {
        max-width: 140px;
      }
      ul li {
        margin-bottom: 25px;
      }
    }
    .text_area2 {
      .cen_logo {
        display: none;
      }
      p.en {
        font-size: 0.5em;
        margin-top: 12px;
        &.w-en {
          font-size: 0.8em;
        }
      }
    }
    .line_share {
      top: initial;
      bottom: 180px;
    }
    .cv_btn {
      width: 110px;
      height: 110px;
      padding: 0;
      bottom: 40px;
      .btn-head {
        padding: 5px;
      }
      .btn-bottm a.btn {
        min-width: 200px;
      }
      img {
        height: 110px;
        width: 110px;
        margin: 0 auto;
      }
    }
    .line_share {
      padding: 10px;
    }
  }
}
.bx-wrapper {
  margin: 0 0 20px !important;
  position: relative;
}
.bx-controls-direction {
  width: 100%;
  position: absolute;
  top: 50%;
}
.bx-controls-direction a {
  width: 32px;
  height: 32px;
  margin: -16px 0 0;
  position: absolute;
  top: 50%;
  z-index: 9999;
  outline: 0;
  text-indent: -9999px;
}
.bx-prev {
  left: 1% !important;
  background: url(./../img/prev.png) no-repeat 0 0;
  background-size: contain;
}
.bx-next {
  right: 1% !important;
  background: url(./../img/next.png) no-repeat 100% 0;
  background-size: contain;
}

/*-----------------------------------------------------------
 #main
-----------------------------------------------------------*/
#main {
  width: 100%;
  margin: 0 auto;
}
.inner {
  width: 95%;
  max-width: 1536px;
  margin: 0 auto;
}
.inner12 {
  width: 95%;
  max-width: 1200px;
  margin: auto;
}
.p-img {
  text-align: center;
  width: 100%;
}

#news {
  color: $base-bg;
  padding: min(5%, 50px) 0;
  border-radius: 30px 30px 0 0;
  width: 100%;
  .inner {
    width: min(90%, 1536px);
    margin: auto;
  }
  .left {
    width: 30%;
    padding: 0 30px 0 min(12vw, 120px);
    h2.mainTit {
      text-align: left;
      margin: 0 auto 0 0;
      .en {
        font-size: clamp(23px, 6vw, 60px);
      }
    }
  }
  .newsList {
    width: min(70%, 1080px);
    li {
      border-bottom: 1px solid $base-line;
      margin: 0;
      line-height: 1.45;
      dl {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 3% 0;
        dt {
          margin: 0 20px 0 0;
          display: flex;
          font-family: "Cinzel", serif;
          .date {
            width: min(100%, 100px);
          }
          .cate {
            width: 100%;
            margin-left: 1em;
          }
        }
        dd {
          width: 100%;
          padding-top: 8px;
          a {
            color: $base-bg;
            cursor: pointer;
          }
        }
      }
      &:last-child {
        border-bottom: none;
      }
    }
  }
  @include thou2 {
    .left {
      padding-left: 0;
    }
  }
  @include mid {
    .flex {
      flex-direction: column;
      .left {
        width: 100%;
        padding: 0;
        h2.mainTit {
          padding-top: 3%;
          display: flex;
          align-items: center;
          font-size: 13px;
          .en {
            margin: 0 0.5em 0 0;
            line-height: 1;
          }
        }
      }
      ul.newsList {
        margin: 12px auto 0;
        width: 100%;
        li {
          width: 100%;
          dt {
            line-height: 1;
          }
          dd {
            font-size: 13px;
          }
        }
      }
    }
  }
}
.whitezone {
  background: white;
  border-radius: 8px;
  padding: 40px;
  margin-top: min(8%, 72px);
  p {
    font-size: 20px;
    font-weight: 700;
    display: inline-block;
    position: relative;
    line-height: 1;
    &::after {
      content: "";
      background: #1a1a1a;
      display: block;
      position: absolute;
      width: 100%;
      height: 2px;
      bottom: -10px;
      left: 0;
    }
  }
}
.content01 {
  position: relative;
  padding: min(8%, 80px) 0 0;
  .flex {
    width: 100%;
    margin: 0 auto min(8%, 80px);
    padding: min(6%, 60px) min(12vw, 120px) min(16%, 160px);
    align-items: flex-end;
    position: relative;
    h2.mainTit {
      text-align: left;
      width: calc(100% - 280px);
    }
    &:after {
      content: "";
      background: url(../img/con01_bg.webp) no-repeat center center/contain;
      width: 506.59px;
      height: 315px;
      display: block;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      z-index: -1;
    }
  }
  .p-img {
    width: 100%;
    overflow: hidden;
    img {
      width: 100%;
      height: 75vh;
      object-fit: cover;
    }
  }
  @include thou2 {
    .flex {
      width: 100%;
      padding: min(6%, 60px) 5% min(16%, 160px);
    }
  }
  @include mid {
    .flex {
      flex-direction: column;
      padding: min(6%, 60px) 5%;
      h2.mainTit {
        width: 100%;
        margin-bottom: min(6%, 60px);
        text-align: center;
      }
      &::after {
        width: 420px;
        height: 300px;
        // right: 50%;
        // top: initial;
        // bottom: 0;
        // transform: translateX(50%);
        right: 50%;
        transform: translate(50%, -55%);
        opacity: 0.3;
      }
    }
  }
}
