.content02 {
  position: relative;
  .flex {
    height: clamp(640px, 82.2vh, 860px);
    overflow: hidden;
    .left {
      padding: 0 min(10%, 120px);
      width: min(60%, 920px);
      h2 {
        text-align: left;
      }
      a.btn {
        margin: min(6%, 60px) auto 0 0;
      }
    }
    .sliderwrap {
      width: min(40%, 616px);
      .slick-list {
        height: 100% !important;
        .slick-slide {
          border: none;
          margin-bottom: 20px;
        }
        img {
          width: 100% !important;
        }
      }
    }
  }
  &:after {
    content: "";
    background: url(../img/con02_bg.webp) no-repeat center center/contain;
    width: 39.69%;
    height: 475px;
    display: block;
    position: absolute;
    left: -5.57%;
    bottom: 0;
    z-index: -1;
  }
  @include ipad {
    padding: min(10%, 80px) 0 0;
    .flex {
      flex-direction: column;
      height: auto;
      .left {
        width: 100%;
        h2.mainTit {
          width: 100%;
          text-align: center;
        }
        a.btn {
          margin: min(6%, 60px) auto min(10%, 100px);
        }
      }
      .sliderwrap {
        width: 100%;
        margin: 5vw;
        .slick-list {
          .slick-slide {
            margin-bottom: 0;
          }
        }
      }
    }
  }
}
.content03 {
  padding: 5%;
  width: 100%;
  .grid {
    display: grid;
    grid-template-columns: 1fr 41.28%;
    align-items: center;
    h2.en {
      font-size: clamp(23px, 5.5vw, 60px);
      font-weight: 400;
      line-height: 1.45;
    }
    dl {
      width: 100%;
      position: relative;
      dt {
        font-size: clamp(20px, 4.2vw, 42px);
        font-family: "Shippori Mincho B1", "游明朝体", "Yu Mincho", YuMincho,
          "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", serif;
        line-height: 1.45;
        margin-bottom: 20px;
        &.w-ch {
          font-family: "Noto Serif SC", serif;
          font-family: 400;
        }
        &.w-co {
          font-family: "Noto Serif KR", serif;
          font-weight: 400;
        }
      }
      &:after {
        content: "";
        background: url(../img/con03_bg.webp) no-repeat center center/contain;
        width: 168.37px;
        height: 168.37px;
        position: absolute;
        top: -22px;
        right: -13.3%;
      }
    }
  }
  .bgba {
    padding: min(10%, 120px) min(10%, 100px) min(4%, 40px);
    width: 100%;
    margin: min(6%, 60px) auto 0;
    border-radius: 12px;
  }
  .eventList {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li {
      width: calc(50% - 30px);
      margin-bottom: min(8%, 60px);
      dd {
        margin-top: 20px;
        .pimg {
          width: 100px;
          height: auto;
          margin-right: min(8%, 80px);
        }
        p {
          color: $base-bg;
          text-align: left;
          width: min(95%, 425px);
          padding-right: 20px;
        }
      }
    }
  }
  .bgyel {
    color: $base-bg;
    text-align: center;
    padding: min(6%, 60px);
    h3 {
      font-size: clamp(18px, 2vw, 20px);
      margin: min(5%, 50px) auto;
      span {
        font-size: 0.8em;
        font-weight: 500;
        display: block;
      }
    }
    dl.whiteZone {
      background: white;
      box-shadow: 0 0 6px rgba(0, 0, 0, 0.16);
      border-radius: 3px;
      width: 100%;
      margin: auto;
      padding: 5%;
      color: $base-bg;
      position: relative;
      dt {
        font-size: clamp(18px, 2.4vw, 24px);
        text-align: center;
        font-weight: 700;
        line-height: 1.45;
        span {
          font-size: 0.8em;
          display: block;
          font-weight: 500;
        }
      }
      .pbox {
        padding: 8px 12px;
        margin-top: 12px;
        border: 2px solid $base-bg;
        display: block;
      }
      &:has(+ dl) {
        margin-bottom: 24px;
        &::before {
          content: "";
          background: url(../img/con02_plus.webp) no-repeat center
            center/contain;
          width: 42px;
          height: 42px;
          z-index: 2;
          display: block;
          position: absolute;
          bottom: -33px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
  }
  @include ipad {
    padding: 40px 5%;
    .grid {
      grid-template-columns: 1fr;
      grid-template-rows: 5em 1fr;
      dd {
        .i-text.mar {
          display: inline;
        }
      }
    }
    .eventList {
      flex-wrap: wrap;
      li {
        width: 100%;
        dd {
          align-items: flex-start;
          p {
            line-height: 1.6;
            padding-right: 3%;
          }
        }
      }
    }
  }
  @include sp {
    .eventList {
      li {
        dd {
          margin-top: 12px;
          p {
            line-height: 1.6;
          }
        }
      }
    }
  }
}
.content04 {
  background: #faf8e5;
  color: $base-bg;
  padding: min(12%, 120px) 0;
  position: relative;
  .tkindList {
    display: flex;
    justify-content: center;
    margin-top: 160px;
    li.tlind-item {
      border: 1px solid $base-bg;
      border-radius: 20px;
      padding: 120px 5% 5%;
      width: min(50%, 542px);
      margin-right: min(7%, 70px);
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      h3 {
        text-align: center;
        font-family: "Shippori Mincho B1", "游明朝体", "Yu Mincho", YuMincho,
          "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", serif;
        font-size: clamp(20px, 3vw, 30px);
        position: absolute;
        top: -100px;
        left: 50%;
        transform: translateX(-50%);
        background: #e6deca;
        border-radius: 3px;
        width: 91%;
        padding: 35px;
        line-height: 1.7;
        span {
          display: block;
          font-size: 15px;
        }
      }
      &:last-child {
        margin-right: 0;
        h3 {
          background: #cae6d8;
        }
      }
      dl {
        background: white;
        border-radius: 8px;
        padding: 25px;
        margin-bottom: 20px;
        width: min(95%, 360px);
        position: relative;
        img {
          max-width: 125px;
        }
        p {
          margin-top: 12px;
          color: $base-bg;
          .pbox {
            border: 1px solid $base-bg;
            padding: 12px 12px 8px;
            margin-top: 12px;
            text-align: center;
            display: block;
          }
        }
        dt {
          text-align: center;
          font-size: clamp(18px, 2.3vw, 23px);
          span {
            display: block;
            font-size: 15px;
          }
        }
        &:last-of-type {
          margin-bottom: 0;
        }
        &:not(:last-of-type) {
          img {
            max-width: 105px;
          }
          &:after {
            content: "";
            width: 42px;
            height: 42px;
            display: block;
            background: url(../img/con04_plus.webp) no-repeat center
              center/contain;
            position: absolute;
            bottom: -30px;
            left: 50%;
            transform: translateX(-50%);
            z-index: 2;
          }
        }
      }
      .priceZone {
        margin-top: 24px;
