.content06 {
  color: white;
  padding: min(12%, 150px) 0;
  position: relative;
  h2.mainTit {
    font-size: clamp(20px, 3.5vw, 35px);
    line-height: 1.45;
    margin-top: min(10%, 120px);
  }
  table {
    width: min(95%, 1080px);
    margin: min(5%, 60px) auto min(10%, 120px);
    display: block;
    tbody {
      width: 100%;
      display: block;
      tr {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        th {
          width: 180px;
          border-bottom: 1px solid $base-line;
          padding: 25px 0;
          text-align: left;
        }
        td {
          width: calc(100% - 180px);
          border-bottom: 1px solid $base-line;
          padding: 25px 0;
        }
        &:first-of-type {
          th,
          td {
            border-top: 1px solid $base-line;
          }
        }
      }
    }
  }
  .bgblu {
    padding: min(10%, 120px);
    border-radius: 12px 0 0 12px;
    margin: 0 0 0 auto;
    width: 91%;
    overflow: hidden;
    h3 {
      font-size: 20px;
      font-family: "Shippori Mincho B1", "游明朝体", "Yu Mincho", YuMincho,
        "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", serif;
      position: relative;
      padding-left: min(2em, 30px);
      &::after {
        content: "";
        background: url(../img/con08_bg.webp) no-repeat center left/contain;
        display: block;
        width: 56px;
        height: 92px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
      }
    }
    ul {
      margin: min(6%, 60px) 8%;
      li {
        position: relative;
        &::before {
          content: "・";
        }
      }
    }
    a {
      background: white;
      border-radius: 3px;
      color: $base-bg;
      display: block;
      width: min(80%, 800px);
      padding: 35px;
      text-align: center;
      font-weight: 700;
      margin: auto;
      transition: 0.2s;
      position: relative;
      z-index: 1;
    }
  }
  @include thou2 {
    padding-bottom: min(6%, 60px);
  }
  @include ipad {
    padding-top: 0;
    table tbody tr {
      flex-direction: column;
      &:first-of-type td {
        border-top: none;
        line-height: 1.6;
      }
      th {
        border-bottom: none;
        padding-bottom: 0;
      }
      td {
        padding-top: 3%;
      }
    }
    .bgblu {
      padding: 5%;
      width: 95%;
      ul {
        margin: min(6%, 60px) 0;
        font-size: 14px;
      }
    }
  }
  @include mid {
    .bgblu a {
      padding: 7%;
      width: 100%;
    }
  }
}
