/*アコーディオン*/
.accordion-area {
  list-style: none;
  width: min(100%, 960px);
  margin: min(6%, 60px) auto 0;
  color: $base-font;
  border-top: 1px solid $base-line;
  li {
    border-bottom: 1px solid $base-line;
    /*アコーディオンタイトル*/
    .acctitle {
      position: relative; /*+マークの位置基準とするためrelative指定*/
      cursor: pointer;
      font-weight: normal;
      padding: 3% 32px 3% 0;
      transition: all 0.5s ease;
      color: white;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      p {
        position: relative;
        font-family: "Shippori Mincho B1", "游明朝体", "Yu Mincho", YuMincho,
          "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", serif;
        &::before {
          content: "Q.";
          font-size: 1.2em;
          margin-right: 0.6em;
          color: #d5cf8e;
          font-family: "Cinzel", serif;
          font-weight: 300;
        }
      }
      .en {
        font-size: clamp(32px, 4.6vw, 46px);
        font-weight: 600;
        width: 20%;
        span {
          font-size: 18px;
          margin-left: 8px;
        }
      }
      &:after {
        /*アイコンの＋と×*/
        content: "";
        position: absolute;
        display: block;
        background: url(../img/event/icon_plus.svg) no-repeat center
          center/contain;
        width: 24px;
        height: 24px;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        transition: 0.2s;
      }
      &.close::after {
        /*　closeというクラスがついたら形状変化　*/
        transform: translateY(-50%) rotate(45deg);
      }
    }
    .accbox {
      /*アコーディオンで現れるエリア*/
      display: none;
      margin-bottom: 3%;
      padding: 0 1.5em 2%;
      color: $base-font;
      font-family: "Shippori Mincho B1", "游明朝体", "Yu Mincho", YuMincho,
        "ヒラギノ明朝 Pro", "Hiragino Mincho Pro", serif;
      p {
        position: relative;
        &::before {
          content: "A.";
          font-size: 1.2em;
          margin-right: 0.6em;
          color: #89d6ff;
        }
      }
    }
  }
}

/* TOP
----------------------------------------------------------- */
.jspContainer {
  width: 100% !important;
  overflow: hidden;
  position: relative;
}
.jspPane {
  width: 100% !important;
  position: absolute;
}
.jspVerticalBar {
  width: 20px;
  position: absolute;
  top: 0;
  right: -5px;
}
.jspArrowUp,
.jspArrowDown {
  margin: 0;
  padding: 0;
  display: block;
  cursor: pointer;
  /* background: url(../img/scroll_top.png) no-repeat center; */
}
.jspArrowDown {
  background: url(../img/scroll_bottom.png) no-repeat center;
}
.jspTrack {
  /* background: url(./../img/scroll_bg1.png) repeat-y; */
  position: relative;
  background-position: center;
}
.jspDrag {
  width: 4px;
  margin: 0 auto;
  background: #6b6b6b;
  position: relative;
  cursor: pointer;
  border-radius: 8px;
  -moz-border-radius: 8px;
  -webkit-border-radius: 8px;
}
.jspArrow.jspDisabled {
  cursor: default;
}
.jspVerticalBar .jspArrow {
  height: 20px;
}
.jspVerticalBar .jspArrow:focus {
  outline: none;
}

